import { Component, h, Element, Prop } from '@stencil/core';

declare const UIkit: any;

@Component({
	tag: 'ks-alert'
})
export class Alert {
    @Element() root: HTMLElement;
    @Prop() message: string = "";

    hashCode: number;

    componentWillLoad() {
        this.hashCode = this.GenerateHash(this.message);
    }

    GenerateHash(str: string) {
        var hash = 0, len = str.length;
        if (str.length === 0) {
            return hash;
        }
        for (let i = 0; i < len; i++) {
            let charC = str.charCodeAt(i);
            hash = ((hash<<5)-hash)+charC;
            hash = hash & hash; // Convert to 32bit integer
        }
        return hash;
    }

    async componentDidLoad() {
        const element = document.querySelector(`#ks-alert-${this.hashCode}`);
        UIkit.modal(element).show();
    }

    Close() {
        const element = document.querySelector(`#ks-alert-${this.hashCode}`);
        UIkit.modal(element).hide().then(()=>{
            this.root.remove();
            element.remove();
        });
    }

	render() {
		return(
            <div id={`ks-alert-${this.hashCode}`} class="uk-modal-full" uk-modal="stack: true">
                <div class="uk-modal-dialog">
                    <div class="uk-background-cover uk-flex uk-flex-between uk-flex-column" uk-height-viewport>

                        <div class="uk-padding-small uk-flex-auto uk-flex uk-flex-center uk-flex-middle uk-flex-column" style={{maxWidth: "1300px", margin: "auto"}}>
                            <p class="uk-text-center ks-text-decorated uk-h1" innerHTML={this.message}></p>
                            <div class="uk-margin-top">
                                <button onClick={()=>this.Close()} class="uk-button uk-button-secondary" style={{padding: "5px 40px"}}>
                                    OK
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}